// reset all default properties of input

input[type=text] {
    padding: .5rem 2rem;
    background-color: $primary-green-color;
    font-size: 1.2rem;
    color: $primary-text-color;
    outline: none;
    border: 1px solid $primary-background-color;
    outline: transparent solid 2px;
    outline-offset: 2px;
    animation: all 1.2s;


    &::placeholder {
        color: rgba( $primary-text-color, 0.75);
    }

    &:focus {
        outline-color: $primary-text-color;
    }
}