.root {
    background: url('../assets/background.png');
    background-size: cover;
    width: 100%;
    padding: 3rem;
    box-sizing: border-box;
}

.options {
    background-color: $primary-background-color;
    padding: 1rem 3rem;

    &__row {
        margin-top: 1rem;
        display: flex;
    }

    &__row-items {
        flex: 1;
        display: flex;
        flex-wrap: wrap;

        & > div {
            flex: 0 0 15rem;
            margin-bottom: 1rem;
        }

    }
}

.main {
    background-color: $primary-background-color;
    margin-top: 5rem;
    padding: 2rem 3rem;
}

.actions {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-items: center;
    user-select: none;

    & > button:first-child {
        background-color: transparent;


        & > svg {
            width: 3rem;
            height: 3rem;
            fill: $primary-text-color;
        }

        &:hover > svg {
            fill: $primary-green-color;
        }
    }

    & > button:last-child {
        
        & > svg {
            width: 3rem;
            height: 3rem;
            fill: currentColor;
        }
    }
    

    & > p {
        position: relative;
        font-weight: 300;
        font-size: 1.2rem;
        line-height: 1.2;
        color: $primary-text-color;
        
        &::before, &::after {
            content: "";
            position: absolute;
            bottom: 0;
            left: -4rem;
            width: 3rem;
            height: 1px;
            background-color: rgba($primary-text-color, 0.4);
        }

        &::after {
            left: auto;
            right: -4rem;
        }
    }

    & > input {
        width: 30rem;
    }
}


.content {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin-top: 2rem;
}

.request {
    width: 30rem;
    padding: 1rem 2rem;
    background: rgba(#000, 0.5);
}

.canvas {
    width: 60rem;
    height: 40rem;
    background: #000;
    margin-left: auto;
    margin-right: auto;
}

.response {
    width: 30rem;
    padding: 1rem 2rem;
    background: rgba(#000, 0.5);
}