
*, 
*::before, 
*::after {
    margin: 0;
    padding: 0;
    box-sizing: inherit;
}

html {
    font-size: 62.50%;
}

body {
    width: 100vw;

    &::-webkit-scrollbar {
        display: none;
    }
}

::selection {
    color: $primary-green-color;
    background-color: #000;
}